<template>
   <div id="view-employee">
      <h3>View Employee</h3>
      <ul class="collection with-header">
         <li class="collection-header">
            <h4>{{ name }}</h4>
         </li>
         <li class="collection-item">EmployeeID#:{{ employee_id }}</li>
         <li class="collection-item">Department#:{{ dept }}</li>
         <li class="collection-item">Position#:{{ position }}</li>
      </ul>
      <router-link to="/" class="btn grey">Back</router-link>
      <button @click="deleteEmployee" class="btn red">Delete</button>

      <!--//@Vue.js & Firestore App - Build & Deploy [Part 5] paramsの使い方を学べる！-->

      <div class="fixed-action-btn">
         <!-- //-routerのnameを使う! -->
         <router-link
            v-bind:to="{
               name: 'edit-employee',
               params: { employee_id: employee_id },
            }"
            class="btn-floating btn-large red"
            ><i class="fa fa-pencil"></i
         ></router-link>
      </div>
   </div>
</template>

<script>
import db from './firebaseInit';
export default {
   name: 'view-employee',
   data() {
      return {
         employee_id: null,
         name: null,
         dept: null,
         position: null,
      };
   },
   //! index.jsでpath: '/:employee_id',と設定しているため
   //¥beforeRouteEnter←componentごと ※beforeEnterではない ←main.jsで使用
   beforeRouteEnter: (to, from, next) => {
      // - ページをロードする前に事前に行う挙動 - firebaseからデータを取ってくる
      console.log(db);
      //!.where('employee_idはfirestoreのdoc内のフィールドのキー)
      db.collection('employees')
         .where('employee_id', '==', to.params.employee_id)
         .get()
         .then(quersySnapshot => {
            //!条件にマッチした配列全て
            console.log(quersySnapshot);
            //!条件にマッチした配列全ての中の各要素
            quersySnapshot.forEach(doc => {
               console.log(doc);
               next(vm => {
                  vm.employee_id = doc.data().employee_id;
                  vm.name = doc.data().name;
                  vm.dept = doc.data().dept;
                  vm.position = doc.data().position;
               });
            });
         });
   },
   //同じコンポーネントでパラメーター変更を検知するためには、 $route オブジェクトを watch するだけです。
   watch: {
      $route: 'fetchData',
   },
   methods: {
      fetchData() {
         console.log($route);
         db.collection('employees')
            .where('employee_id', '==', this.$route.params.employee_id)
            .get()
            .then(quersySnapshot => {
               quersySnapshot.forEach(doc => {
                  this.employee_id = doc.data().employee_id;
                  this.name = doc.data().name;
                  this.dept = doc.data().dept;
                  this.position = doc.data().position;
               });
            });
      },
      deleteEmployee() {
         if (confirm('Are you sure?')) {
            db.collection('employees')
               .where('employee_id', '==', this.$route.params.employee_id)
               .get()
               .then(quersySnapshot => {
                  quersySnapshot.forEach(doc => {
                     doc.ref.delete();
                     this.$router.push('/');
                  });
               });
         }
      },
   },
};
</script>



