<template>
   <div id="dashboard">
      <h3>Dashboard</h3>
      <ul class="collection with-header">
         <li class="collection-header"><h4>Employees</h4></li>

         <li
            v-for="employee in employees"
            :key="employee.id"
            class="collection-item"
         >
            <div class="chip">{{ employee.dept }}</div>
            {{ employee.employee_id }}:{{ employee.name }}
<!--
            //¥v-bindでtoをしばり、nameとparamsを設定する！！
            //TODO! params: {employee_id: ~ }はindex.jsのparamsを/:employee_idとっ設定しているため
 -->
            <router-link
               class="secondary-content"
               :to="{
                  name: 'view-employee',
                  params: { employee_id: employee.employee_id },
               }"
               ><i class="fa fa-eye"></i
            ></router-link>
         </li>
      </ul>

      <div class="fixed-action-btn">
         <router-link to="/new" class="btn-floating btn-large red"
            ><i class="fa fa-plus"></i
         ></router-link>
      </div>
   </div>
</template>

<script>
//-データベースの呼び出し！
import db from './firebaseInit';
export default {
   name: 'dashboard',
   data() {
      return {
         employees: [],
      };
   },
   // TODO mountedであれば更新された内容が表示される？？
   created() {
      console.log('いつ発動?');
      //!orderBy 順番の基準を決められる
      //https://firebase.google.com/docs/firestore/query-data/order-limit-data
      db.collection('employees')
         .orderBy('dept')
         .get()
         .then(querySnapshot => {
            querySnapshot.forEach(doc => {
               // console.log(doc);
               // console.log(doc.id);
               // console.log(doc.data());
               const data = {
                  id: doc.id,
                  employee_id: doc.data().employee_id,
                  name: doc.data().name,
                  dept: doc.data().dept,
                  position: doc.data().position,
               };
               this.employees.push(data);
               // console.log(this.employees);
            });
         });
   },
};
</script>



