<template>
   <div id="edit-employee">
      <!-- //^ViewEmployee.vueとほぼ一緒のHTML -->
      <h3>Edit Employee</h3>
      <div class="row">
         <form @submit.prevent="updateEmployee" class="col s12">
            <div class="row">
               <div class="input-field col s12">
                  <input type="text" v-model="employee_id" required disabled />
               </div>
            </div>
            <div class="row">
               <div class="input-field col s12">
                  <input type="text" v-model="name" required />
               </div>
            </div>
            <div class="row">
               <div class="input-field col s12">
                  <input type="text" v-model="dept" required />
               </div>
            </div>
            <div class="row">
               <div class="input-field col s12">
                  <input type="text" v-model="position" required />
               </div>
            </div>
            <button type="submit" class="btn">
               Submit
            </button>
            <router-link to="/" class="btn grey">Cancel</router-link>
         </form>
      </div>
   </div>
</template>

<script>
import db from './firebaseInit';
export default {
   name: 'edit-employee',
   data() {
      return {
         employee_id: null,
         name: null,
         dept: null,
         position: null,
      };
   },
   //! index.jsでpath: '/:employee_id',と設定しているため
   //¥beforeRouteEnter←componentごと ※beforeEnterではない ←main.jsで使用
   beforeRouteEnter: (to, from, next) => {
      // - ページをロードする前に事前に行う挙動 - firebaseからデータを取ってくる
      console.log(db);
      //!.where('employee_idはfirestoreのdoc内のフィールドのキー)
      db.collection('employees')
         .where('employee_id', '==', to.params.employee_id)
         .get()
         .then(quersySnapshot => {
            //!条件にマッチした配列全て
            console.log(quersySnapshot);
            //!条件にマッチした配列全ての中の各要素
            quersySnapshot.forEach(doc => {
               console.log(doc);
               next(vm => {
                  vm.employee_id = doc.data().employee_id;
                  vm.name = doc.data().name;
                  vm.dept = doc.data().dept;
                  vm.position = doc.data().position;
               });
            });
         });
   },
   //同じコンポーネントでパラメーター変更を検知するためには、 $route オブジェクトを watch するだけです。
   watch: {
      $route: 'fetchData',
   },
   methods: {
      fetchData() {
         console.log($route);
         db.collection('employees')
            .where('employee_id', '==', this.$route.params.employee_id)
            .get()
            .then(quersySnapshot => {
               quersySnapshot.forEach(doc => {
                  this.employee_id = doc.data().employee_id;
                  this.name = doc.data().name;
                  this.dept = doc.data().dept;
                  this.position = doc.data().position;
               });
            });
      },
      updateEmployee() {
         db.collection('employees')
            .where('employee_id', '==', this.$route.params.employee_id)
            .get()
            .then(quersySnapshot => {
               quersySnapshot.forEach(doc => {
                  doc.ref
                     .update({
                        employee_id: this.employee_id,
                        name: this.name,
                        dept: this.dept,
                        position: this.position,
                     })
                     .then(() => {
                        console.log('aa');
                        this.$router.push({
                           name: 'view-employee',
                           params: { employee_id: this.employee_id },
                        });
                     });
               });
            });
      },
   },
};
</script>



